<template>
  <span :class="['badge', statusClassName]">{{ statusText }}</span>
</template>
<script>
import { ref, watch }  from 'vue'
export default {
  props: {
    title: String,
    type:  {
      type: String,
      default: 'badge',
      validator(value) {
        return ['active', 'done', 'cancelled','badge', 'outdated', 'performed'].includes(value)
      }
    }
  },
  setup(props) {
    const statusClasses = {
      active: 'primary',
      done: 'primary',
      performed: 'primary',
      cancelled: 'danger',
      outdated: 'warning',

    }
    const statusTexts = {
      active: 'Активен',
      done: 'Выполнен',
      performed: 'Выполненяется',
      cancelled: 'Отменен',
      outdated: 'Просрочен'
    }
    const statusClassName = ref(statusClasses[props.type])
    const statusText = ref(statusTexts[props.type])

    watch(props, val => {
      statusClassName.value = statusClasses[val.type]
      statusText.value = statusTexts[val.type]
    })
    // watch(props, val => {
    //   className.value = classesMap[val.type]
    //   text.value = textMap[val.type]
    // })

    return { statusClassName, statusText }
  }

}
</script>
