<template>
  <div v-if="tasks.length">
    <h3 class="text-white">Всего активных задач: {{ activeTaskCounter }}</h3>
    <div :id="idx" class="card" v-for="(task, idx) of tasks" :key="task.id">
      <h2 class="card-title">
        {{ task.title }}
        <AppStatus :type="task.status" />
      </h2>
      <p>
        <strong>
          <small>
            {{new Date(task.date).toLocaleDateString()}}
          </small>
        </strong>
      </p>
      <router-link :to="'/task/' + task.id" class="btn primary">Посмотреть</router-link>
    </div>
  </div>
  <h1 class="text-white center" v-else>Задач пока нет</h1>
</template>

<script>
import AppStatus from '../components/AppStatus'
import { computed } from "vue";
import store from "@/store";

export default {
  setup() {
    const tasks = computed(() => {
      return store.getters.tasks;
    });
    const activeTaskCounter = computed(() => {
      return store.getters.activeTasksCount
    })

    return{
      tasks,
      activeTaskCounter
    }
  },
  components: {AppStatus}
}
</script>
